import React, { FC } from 'react'
import clsx from 'clsx'

import { SkeletonProps } from './types'
import './Skeleton.scss'

const Skeleton: FC<SkeletonProps> = ({
  children,
  animation,
  backgroundColor,
  variant,
  isLoading,
  width,
  height,
  className,
  display = 'inlineFlex',
}) => {
  return (
    <span
      className={clsx(
        isLoading && 'Skeleton Skeleton--root',
        `--${display}`,
        isLoading && animation && `Skeleton--${animation}`,
        isLoading && variant && `Skeleton--${variant}`,
        className,
      )}
      style={{ backgroundColor, width, height }}
    >
      {children}
    </span>
  )
}

Skeleton.defaultProps = {
  variant: 'text',
  animation: 'pulse',
  backgroundColor: '#E6E6E6',
}

export default Skeleton
